import React from 'react';
import PropTypes from 'prop-types';
import { useStore } from '../store';

function partial(props) {
    const { data, index, handleClick, selectedIndex, childRefs } = props;
    const {
        action,
        disp: { metaData },
    } = data;

    const title = `${metaData.peopleFirstName} ${metaData.peopleLastName}`;

    return (
        <div
            ref={childRefs}
            role="row"
            id={`${index}`}
            key={`result-people-${index}`}
            aria-selected={selectedIndex === index}
            className="autocomplete-concierge__search-result-item"
            onClick={(e) => handleClick(e, action, title)}
            onKeyDown={(e) => handleClick(e, action, title)}
            tabIndex={-1}
        >
            <a title={title} className="title" href={action}>
                {title}
            </a>

            <div className="details" role="gridcell">
                {metaData.peopleDepartment && (
                    <div className="autocomplete-results__metadata">
                        <svg className="svg-icon">
                            <use href="#building" />
                        </svg>
                        {metaData.peopleDepartment.replace(',', ', ')}
                    </div>
                )}

                {metaData.peoplePhone && (
                    <div className="autocomplete-results__metadata">
                        <svg className="svg-icon">
                            <use href="#phone" />
                        </svg>
                        <span className="sr-only">Phone number</span>
                        {metaData.peoplePhone}
                    </div>
                )}

                {metaData.peopleEmail && (
                    <div className="autocomplete-results__metadata">
                        <svg className="svg-icon">
                            <use href="#email" />
                        </svg>
                        <span className="sr-only">Email</span>
                        {metaData.peopleEmail}
                    </div>
                )}
            </div>
        </div>
    );
}

export default function People(props) {
    const { id, label, className, templateId, childRefs, trackedClick } = props;
    const { results, selectedIndex } = useStore();

    const handleClick = (e, action, title) => {
        e.preventDefault();
        // eslint-disable-next-line no-return-assign
        trackedClick(action, title, () => (document.location.href = action));
    };

    if (!results[templateId] || results[templateId].length === 0) {
        return null;
    }

    return (
        <div
            id={`result-row-${id}`}
            className={[`autocomplete-concierge__section`, className].join(' ')}
            role="rowgroup"
        >
            <h2 className="autocomplete-concierge__section-title">{label}</h2>
            {results[templateId] &&
                results[templateId].map((data, index) =>
                    partial({
                        data,
                        index,
                        handleClick,
                        selectedIndex,
                        childRefs,
                    })
                )}
        </div>
    );
}

const { string, number, func } = PropTypes;

People.propTypes = {
    id: string.isRequired,
    label: string,
    className: string,
    templateId: number.isRequired,
    childRefs: func.isRequired,
};

People.defaultProps = {
    label: ``,
    className: ``,
};
